import sha1 from '@/assets/hmac-sha1.js';
import base64 from '@/assets/base64.js';


export default{
  getToken: function (url, secret, value, method = 'POST') {
    // console.log(url)
    // console.log(secret)
    // console.log(value)
    var arr = []
    // var data = data
    var keys = Object.keys(value).sort();
    // console.log(keys)
    for (var k of keys) {
      arr.push(encodeURIComponent(k) + '%3D' + encodeURIComponent(value[k]))
    }
    var sign_string = method + "&" + encodeURIComponent(url) + '&' + arr.join("%26")
    var sign_secret = secret + "&"
    // console.log('sign_string:' + sign_string)
    // console.log('sign_secret:' + sign_secret)
    var sign = sha1.CryptoJS.HmacSHA1(sign_string, sign_secret)
    var out = new ArrayBuffer(sign.sigBytes);
    var dv = new DataView(out);
    for (var i = 0; i < sign.words.length; i++) {
      dv.setInt32(i * 4, sign.words[i])
    }
    return base64.encode(out);
  },
  getTALToken: function (key,timer,secret) {
    var sign_string = key + timer+ secret
    // console.log('sign_string:' + sign_string)
    // console.log('sign_secret:' + sign_secret)
    var sign = sha1.CryptoJS.SHA1(sign_string)
    // console.log(sign)
    var out = new ArrayBuffer(sign.sigBytes);
    var dv = new DataView(out);
    for (var i = 0; i < sign.words.length; i++) {
      dv.setInt32(i * 4, sign.words[i])
    }
    var hexArr = Array.prototype.map.call(
      new Uint8Array(out),
      function (bit) {
        return ('00' + bit.toString(16)).slice(-2)
      }
    )
    return hexArr.join('');
  }

}